.nav-logo{
    background-color: white;
    border-radius: 25%;
    padding: 5px;
    height: 40px;
    width: 40px;
}

.navlink{
    font-family: 'Playfair Display', serif;
    font-size:1rem;
    color: white;
}
.home-div{
    font-family: 'Odibee Sans', cursive;
    text-align: center;
    width: 50%;
    color:white;
    background-color: black;
    opacity: 0.8;
    margin: auto;    
}
.home-div > h1{
    font-size: 4em;
}
.home-div > p{
    font-size: 2em;
}
.thumbnail{
    width: 90%;    
}
#Certificates{
    place-content: start center;
}
.certificate-container{
    place-self: start center;
    margin:auto;
    display:grid;
    grid-template-columns: repeat(auto-fit, minmax(500px,1fr));
    grid-template-rows: repeat(auto-fit,400px);
    grid-gap: 2em;
    gap: 2em;
}
#title{
    text-align: center;
    width: -webkit-max-content;
    width: max-content;
}
.thumbnail{
    place-self: start center;
    width: 90%;    
}
