    font-family: 'Odibee Sans', cursive;
    text-align: center;
    width: 50%;
    color:white;
    background-color: black;
    opacity: 0.8;
    margin: auto;    
}
.greetings{
    background-color: black;
    opacity: 0.8;
    place-content: center center;
}
a,h4,.link{
    font-family: 'Odibee Sans', cursive;
    text-align: center;
    color: white;
